<template>
  <div id="footer" :class="{'detail':isIndex=='0'}">
    <div class="fglo_bg">
      <div id="js_link" class="link fl">
        <div class="link_c">
          <div>
            <span style="color: rgb(136, 136, 136); margin-right: 20px;"></span>
            <a :href="item.link" target="_blank"
            v-for="(item,i) in list" :key="i">{{item.name}}</a>
            <!-- <a href="http://www.didiaokan2018.com/#/" target="_blank"></a><a href="http://www.24zhiboba.com/#/" target="_blank"></a><a
              href="http://www.jrszhibo2018.com/#/" target="_blank">NBA</a><a href="http://www.94zuqiu.com/"
              target="_blank"></a> -->
              </div>
        </div>
      </div>
      <div class="foot_global">
        <p>80</p>
        <p>zhibomei2019 </p>
        <p>Copyright @ 2018 80</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      isIndex: {
        default () {
          return 0
        },
        type: Number
      }
    },
    name: 'Footer',
    data(){
      return {
        list:[]
      }
    },
    created() {
      this.api.getFriendsList().then((resp)=>{
        if(resp.status == 200){
          this.list = resp.data;
        }
      })
      .catch((er)=>{
        console.log(er)
      });
    }
  }

</script>
<style lang="scss">
</style>



